@import url(https://fonts.googleapis.com/css2?family=Bangers&family=Righteous&family=Roboto:wght@300;400;500;600;700&display=swap);
html,
body,
.container-fluid,
.row,
#root,
.App {
  height: 100%;
}

.clickable {
  cursor: pointer;
}

.btn-danger {
  background-color: #94000e;
  border-color: #94000e;
}

.btn-success {
  background-color: #459256;
  border-color: #459256;
}

.alert-danger {
  color: #ffffff;
  background-color: #c11d2c;
  border-color: #c11d2c;
}

.modal .modal-content {
  background-color: #313131;
  color: #fff;
}

.modal .close {
  color: #fff;
}

body{font-family:'Roboto', sans-serif;background-color:#131313;color:#fff}body.light-mode{background-color:#eee;color:#333}body.light-mode .modal-content{background-color:#f1f1f1;color:#333}body.light-mode .added-player-wrapper{background-color:#fff;color:#333}body.light-mode table{color:#333 !important}body.light-mode .category{background-color:#dadada}body.light-mode .category.active{color:#fff}body.light-mode .perk-wrapper{color:#fff}.minigame{position:absolute;top:0;left:0;background-color:#4c3b77;padding:1rem;height:100%;width:100%}h1,h2,h3,h4,h5{font-family:'Bangers', cursive;letter-spacing:0.05em !important}div.version{font-size:0.9rem;margin-top:1rem;color:rgba(255,255,255,0.3)}

.added-player-wrapper {
  background-color: #353535;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-radius: 0.5rem;
}

div.button-container {
  max-width: 500px;
}

div.button-container button {
  font-weight: bold;
}

.theme-wrapper {
  position: absolute;
  top: 0;
  background-color: #000;
  width: 100%;
  left: 0;
  height: 100%;
  padding: 1rem;
  text-align: center;
}

.theme-wrapper .btn-next {
  height: 20rem;
}

.btn-trigger {
  padding: 3rem;
  font-size: 3rem;
  height: 20rem;
}

.perk-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #2e8230;
  position: absolute;
  top: 0;
  left: 0;
  padding: 1rem;
  width: 100%;
  height: 100%;
}

#landmine {
  background-color: #c11919;
}

.perk-icon {
  font-size: 9rem;
}

img {
  width: 100%;
  max-width: 800px;
}

.winner-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  background-color: #2e8230;
  position: absolute;
  top: 0;
  left: 0;
  padding: 1rem;
  width: 100%;
  height: 100%;
}

.perk-icon {
  font-size: 9rem;
}

.category {
  font-family: 'Bangers', cursive;
  letter-spacing: 0.05em !important;
  background-color: #2b2b2b;
  font-size: 1.5rem;
  border-radius: 0.5rem;
}

.category.active {
  background-color: rgb(85, 76, 38);
}

.category.active.music {
  background-color: #d2691e;
}

.category.active.history {
  background-color: #5f9ea0;
}

.category.active.sports {
  background-color: #426482;
}

.category.active.games {
  background-color: #3e8444;
}

.category.active.technology {
  background-color: #525252;
}

.category.active.food {
  background-color: #7b1d1d;
}

.show-turn-wrapper {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  padding-bottom: 5rem;
}

.number-input {
  font-size: 2.5rem;
  text-align: center;
  background-color: #147362;
  color: #fff;
  margin-bottom: 1rem;
  -webkit-text-security: disc;
}

.number-input:focus {
  background-color: #147362;
  color: #fff;
}

.submit-btn {
  font-size: 2rem;
  width: 100%;
}

h1 {
  text-shadow: 3px 5px 2px #000;
}

h1.answer {
  color: #0f0;
}

div.minigame {
  background-color: #438862;
}

div.example {
  margin-bottom: 2rem;
  background-color: rgb(84 84 84);
  border-radius: 0.5rem;
  padding: 0.5rem;
}

div.cw-btn {
  font-size: 1.5rem;
  font-weight: bold;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 3rem;
  margin-bottom: 1rem;
  text-align: center;
  background-color: #694e9e;
  border-radius: 0.5rem;
}

div.cw-display {
  margin-bottom: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

div.cw-display .number {
  color: #0f0;
  font-size: 3rem;
  letter-spacing: 0.5rem;
}

div.cw-display .name {
  color: #555;
  font-size: 3rem;
}


